<template>
    <div class="login">
        <img src="../../assets/img/logo.png">
        <h1>文档管理系统</h1>
        <div class="wholeContainer">
            <h1>登录<hr></h1>
            <div class="inputContainer">
                <input type="text" placeholder="请输入学号 / 教工号" v-model="sNo">
            </div>
            <div class="inputContainer">
                <input type="password" placeholder="请输入密码" v-model="password">
            </div>
            <button @click="loginRequest">登录</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import {mapActions} from 'vuex'
export default {
    data(){
        return{
            sNo:"",
            password:""
        }
    },
    methods:{
        ...mapActions('teach',['setTid']),
        ...mapActions('stu',['setSId']),
        loginRequest(){
            console.log(this.$route)
            let that=this;
            axios.post(`/user/login?account=${this.sNo}&passWord=${this.password}`)
                .then((res)=>{
                    if(res.data.msg=="登录成功"){
                        if(res.data.usertype==="1"){ // 教师
                            this.setTid(res.data.userInfoId)
                            that.$router.push('/teacher')
                        }else if(res.data.usertype==="2"){ // 学生
                            this.setSId(res.data.userInfoId)
                            that.$router.push('/student')
                        }
                    }else{
                        alert('账户或者密码错误')
                    }
                })
        }
    }
}
</script>

<style lang="less" scoped>
@import url("../../assets/less/define.less");

.login{
    width: 100%;
    height: 575px;
    margin: 0 auto;
    text-align: center;
    background-color: #f8fafc;
    >h1{
        margin: 10px;
    }
    .wholeContainer{
        width: 380px;
        height: 310px;
        margin: 20px auto 0 auto;
        padding: 30px;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(102,102,102,.5);
        background-color: #fff;
        h1{
            margin: 0px;
            width: 55px;
            font-size: 24px;
            text-align: left;
            color: @topicDeepBColor;
            hr{
                margin: 10px 0px 0px 20px;
                width: 20px;
                height: 4px;
                text-align: right;
                background-color:@topicDeepBColor;
                border: none;
            }
        }
        .inputContainer{
            margin: 0 auto;
            font-size: 18px;
            input{
                width: 360px;
                height: 40px;
                padding: 10px;
                margin-top: 25px;
                border-radius: 10px;
                border: none;
                font-size: 20px;
                outline: none;
                background-color: #f8fafc;
            }
        }
        button{
            width: 360px;
            height: 40px;
            margin-top: 30px;
            border-radius: 20px;
            border: none;
            font-size: 20px;
            background-color: @topicShallowBColor;
            color: #fff;
            outline: none;
            cursor: pointer;
            &:hover{
                background-color: @topicDeepBColor;
            }
        }
    }
}
</style>
