<template>
    <div class="fileDownload">
        <h1 class="courseName">{{courseInfo.courseName}}</h1>
        <span class="source">{{type | parentName}}>{{type | childName}}</span>
        <div class="handleBtn">
            <el-button type="primary" round>下载</el-button>
            <el-button type="info" round @click="$emit('back')">返回</el-button>
        </div>
        <div class="downloadDetail">
            <el-table
                ref="multipleTable"
                :data="detailFile"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="courseFileName"
                    label="教学文档"
                    width="600"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="downloadSingle(scope.row)" type="text" size="small">下载</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
import axios from 'axios'
export default {
    computed:{
        ...mapGetters("stu",{
            courseInfo:"getCourseInfo",
            detailFile:"getDetailFile"
        })
    },
    props:["type"],
    filters:{
        parentName:function(val){
            if(val<=4){
                return '学期授课资源'
            }else{
                return '学期作业文档'
            }
        },
        childName:function(val){
            let arr=['教学文档','教学案例','教学资源','其他','实验报告模板','平时作业模板','其他'];
            return arr[val+1];
        }
    },
    methods:{
        downloadSingle(data){
            console.log('data.courseFileId: ', data.courseFileId);
            axios.get(`/student/courseFile/download?idList=${[data.courseFileId]}`,{
                responseType: 'blob'
                }).then(res=>{
                    let code=res.data.code;
                    if((code>=200&&code<300)||code==304){
                        const a = document.createElement('a');
                        a.href = `/student/courseFile/download?idList=${[data.courseFileId]}`;
                        document.body.appendChild(a);
                        a.click();
                        document.body.removeChild(a);
                        this.$message({
                            type: 'success',
                            message:res.data.msg
                        });
                    }else{
                        this.$message({
                            type: 'error',
                            message:res.data.msg
                        });
                    }
                }).catch(err=>{
                    
                })
        }
    }
}
</script>

<style lang="less" scoped>
.fileDownload{
    .courseName{
        font-size: 18px;
    }
    .handleBtn{
        float: right;
    }
}
</style>
