<template>
    <div class="search">
        <div class="searchContainer">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item label="老师" v-show="!isUpdate">
                    <el-input v-model="teacher" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="课程">
                    <el-input v-model="course" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="班级" v-show="isUpdate">
                    <el-input v-model="classNo" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="search">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <UpdateDetail v-if="isUpdate&&isSearch"></UpdateDetail>
        <SearchDetail v-if="isSearch&&!isUpdate" @showDetail="showDetail" @setChooseContent="setChooseContent"></SearchDetail>
        <courseDetail v-if="isDetail&&!isUpdate" :chooseContent="chooseContent"></courseDetail>
        <Directory @changeContent="changeContent"></Directory>
    </div>
</template>

<script>
import courseDetail from './courseDetail.vue'
import SearchDetail from './searchDetail.vue'
import UpdateDetail from './updateDetail.vue'
import Directory from './directory.vue'
import {mapActions} from 'vuex'
export default {
    data(){
        return {
            isSearch:false,
            isDetail:false,
            isUpdate:true,
            teacher:"",
            course:"",
            classNo:"",
            chooseContent:''
        }
    },
    components:{
        courseDetail,
        SearchDetail,
        Directory,
        UpdateDetail
    },
    methods:{
        ...mapActions('stu',["requestSearchContent","requestUpdateTable"]),
        // 搜索
        search(){
            if(this.teacher!==""&&this.course!==""&&!this.isUpdate){
                this.isSearch=true;
                this.requestSearchContent({teacher:this.teacher,course:this.course})
                console.log(`teacher:${this.teacher} course:${this.course}`)
            }else if(this.course!==""&&this.classNo!==""&&this.isUpdate){
                this.isSearch=true;
                this.requestUpdateTable({course:this.course,classNo:this.classNo})
            }
        },
        // 显示具体内容
        showDetail(){
            this.isDetail=!this.isDetail;
            this.isSearch=!this.isSearch;
        },
        // 选中查看内容
        setChooseContent(content){
            this.chooseContent=content;
        },
        // 切换内容
        changeContent(type){
            if(type===0){
                if(!this.isUpdate)
                    this.clear()
                this.isUpdate=true
            }else{
                if(this.isUpdate)
                    this.clear()
                this.isUpdate=false
            }
        },
        // 关闭目录清空输入框
        clear(){
            this.isSearch=false;
            this.teacher="";
            this.course="";
            this.classNo="";
        }
    }
}
</script>

<style lang="less" scoped>
@import url("../../assets/less/define.less");

.search{
    width: 1000px;
    height: 600px;
    padding: 20px;
    box-shadow: 0px 0px 4px @topicDeepBColor;
}
</style>
