<template>
    <div class="courseFile">
        <div>
            <el-select v-model="selectCourse" clearable filterable placeholder="请选择相应的课程">
                <el-option
                    v-for="item in myCourse"
                    :key="item.courseInfoId"
                    :label="item.courseName"
                    :value="item.courseName">
                </el-option>
            </el-select>
            <el-select v-model="selectClass" clearable filterable placeholder="请选择相应的班级">
                <el-option
                    v-for="item in myClass"
                    :key="item"
                    :label="item"
                    :value="item">
                </el-option>
            </el-select>
            <el-table
                ref="multipleTable"
                :data="realCourseFile"
                tooltip-effect="dark"
                style="width: 100%">
                <el-table-column
                    prop="courseName"
                    label="课程名称"
                    width="200"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="courseFolderName"
                    label="作业夹名称"
                    width="200"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="上课班级"
                    align="center"
                    prop="courseClass">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="downloadClamp(scope.row)" type="text" size="small">下载</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters ,mapActions} from 'vuex'
export default {
    data(){
        return{
            selectCourse:'',
            selectClass:'',
            realCourseFile:null,
            myClass:[],
        }
    },
    computed:{
        ...mapGetters('teach',{
            myCourse:'getMyCourse',
            courseFile:'getCourseFile',
        })
    },
    watch:{
        selectCourse(val){
            this.realCourseFile=this.courseFile.filter(content=>{
                if(this.selectClass==="")
                    return val===""||content.courseName===val
                else
                    return (val===""||content.courseName===val)&&content.courseClass===this.selectClass
            })
        },
        selectClass(val){
            this.realCourseFile=this.courseFile.filter(content=>{
                if(this.selectCourse==="")
                    return val===""||content.courseClass===val
                else
                    return (val===""||content.courseClass===val)&&content.courseName===this.selectCourse
            })
        },
    },
    mounted(){
        this.requestFolder();
        this.realCourseFile=JSON.parse(JSON.stringify(this.courseFile));
        for(let i=0;i<this.courseFile.length;i++){
            if(!this.myClass.includes(this.courseFile[i].courseClass))
                this.myClass.push(this.courseFile[i].courseClass)
        }
    },
    methods:{
        ...mapActions('teach',["requestFolder","downloadCourseFolder"]),
        downloadClamp(content){
            this.downloadCourseFolder({content,that:this})
        }
    }
}
</script>

<style scoped>
.el-select{
    margin-right: 20px;
}
</style>
