<template>
    <div class="fileDownload">
        <h1 class="courseName">{{courseInfo.name}}</h1>
        <span class="source">{{type | parentName}}>{{type | childName}}</span>
        <div class="handleBtn">
            <el-button type="primary" round >{{handleType | setHandle}}</el-button>
            <el-button type="info" round @click="$emit('back')">返回</el-button>
        </div>
        <div class="downloadDetail">
            <el-table
                ref="multipleTable"
                :data="detailFile"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="courseFileName"
                    label="教学文档"
                    width="600"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">{{handleType | setHandle}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

export default {
    computed:{
        ...mapGetters("teach",{
            courseInfo:"getCourseInfo",
            detailFile:"getDetailFile"
        })
    },
    props:["handleType","type"],
    filters:{
        parentName:function(val){
            if(val<=4){
                return '学期授课资源'
            }else{
                return '学期作业文档'
            }
        },
        childName:function(val){
            let arr=['教学文档','教学案例','教学资源','其他','实验报告模板','平时作业模板','其他'];
            return arr[val-1];
        },
        setHandle:function(val){
            if(val===0){
                return '上传'
            }else if(val===1){
                return '下载'
            }else if(val===2){
                return '修改'
            }else if(val===3){
                return '删除'
            }
        },
    },
    methods:{
        ...mapActions('teach',['changeFolderData','deleteFolderData']),
        // 点击时进行的操作
        handleClick(content){
            if(this.handleType===2){
                 this.$prompt(`修改${content.courseName}的名字为`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({ value }) => {
                    if(value===content.courseName){
                        this.$message({
                            type:'info',
                            message:'输入的名字与当前名字相同，请重新输入'
                        })
                    }else{
                        this.changeFolderData({content,that:this});
                    }
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'+err
                    });       
                });
            }else if(this.handleType===3){
                this.deleteFolderData({content,that:this});
            }
        }
    }
}
</script>

<style lang="less" scoped>
.fileDownload{
    .courseName{
        font-size: 18px;
    }
    .handleBtn{
        float: right;
    }
}
</style>
