<template>
    <div class="newCourse">
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="课程名称">
                <el-input v-model="course" placeholder=""></el-input>
            </el-form-item>
            <el-button type="primary" @click="create()">确定创建</el-button>
        </el-form>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
import axios from 'axios'
export default {
    data(){
        return{
            course:""
        }
    },
    computed:{
        ...mapGetters('teach',{
            id:'getId'
        })
    },
    methods:{
        create(){
            let that=this;
            if(this.course!==""){
                this.$confirm('是否确定创建新课程?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        axios.post(`/teacher/courseInfo/createCourse?userInfoId=${parseInt(that.id)}&courseName=${that.course}`)
                        .then(res=>{
                            this.$message({
                                type: 'success',
                                message: res.data.msg
                            });
                        }).catch(err=>{

                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消创建'
                        });          
                });
            }
        }
    }
}
</script>

