<template>
    <div class="searchDetail">
        <el-table
            :data="searchContent"
            tooltip-effect="dark"
            style="width: 100%"
            stripe
            >
            <el-table-column
                prop="teacherName"
                label="老师"
                width="240">
            </el-table-column>
            <el-table-column
                prop="courseName"
                label="课程"
                width="400">
            </el-table-column>
            <el-table-column
                label="操作"
                width="100">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import {mapGetters , mapActions} from 'vuex'
export default {
    data(){
        return{
            
        }
    },
    methods:{
        handleClick(msg){
            console.log('msg: ', msg);
            this.$emit('setChooseContent',msg);
            this.$emit('showDetail');
        }
    },
    computed:{
        ...mapGetters('stu',{
            searchContent:"getSearchContent"
        })
    }
}
</script>

