<template>
    <div class="container">
        <NewCourse v-if="type===0"></NewCourse>
        <MyCourse v-if="type===1"></MyCourse>
        <CourseFile v-if="type===2"></CourseFile>
        <Directory @changeContent="changeContent"></Directory>
    </div>
</template>

<script>
import NewCourse from './newCourse.vue'
import MyCourse from './myCourse.vue'
import CourseFile from './courseFile.vue'
import Directory from './directory.vue'
export default {
    data(){
        return{
            type:0
        }
    },
    components:{
        NewCourse,
        MyCourse,
        CourseFile,
        Directory
    },
    methods:{
        changeContent(type){
            this.type=type;
        }
    }
}
</script>


<style lang="less" scoped>
@import url("../../assets/less/define.less");
.container{
    width: 1000px;
    height: 600px;
    padding: 20px;
    box-shadow: 0px 0px 4px @topicDeepBColor;
}
</style>
