<template>
    <div class="directory">
        <!-- <el-button class="directoryBtn" type="primary"  @click="showDrawer"></el-button> -->
        <button class="directoryBtn" icon="el-icon-caret-right"></button>
        <el-drawer
            title="可选操作"
            :visible.sync="drawer"
            :direction="direction"
            :before-close="close"
            :size="'20%'">
            <el-menu
                default-active="1"
                class="el-menu-vertical-demo">
                <el-menu-item index="1" @click="turn(0)">
                    <span slot="title">课程建立</span>
                </el-menu-item>
                <el-menu-item index="2" @click="turn(1)">
                    <span slot="title">我的课程</span>
                </el-menu-item>
                <el-menu-item index="3" @click="turn(2)">
                    <span slot="title">课程作业</span>
                </el-menu-item>
            </el-menu>
        </el-drawer>
    </div>
</template>

<script>
export default {
    data(){
        return{
            drawer: false,
            direction: 'ltr',
        }
    },
    methods:{
        close(done){
            done();
        },
        showDrawer(){
            this.drawer=true;
        },
        turn(type){
            this.drawer=false;
            this.$emit('changeContent',type)
        }
    }
}
</script>

<style>
.directoryBtn{
    position: fixed;
    left: 0px;
    top: 50%;
    width: 10px;
    height: 80px;
    font-size:20px;
}
</style>
