<template>
    <div class="courseDetail">
        <div class="searchList" v-if="!searchDetail">
            <h1>{{courseInfo.courseName}}</h1>
            <el-table
                :data="[courseInfo]"
                style="width: 100%"
                border>
                <el-table-column 
                    label="学期授课资源"
                    align="center">
                    <el-table-column 
                        label="教学文档"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad(1)" 
                            type="text" 
                            :disabled="courseInfo.teachingDocument=='无'?true:false" 
                            size="small">{{courseInfo.teachingDocument}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学案例"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad(2)" 
                            type="text" 
                            :disabled="courseInfo.teachingCase=='无'?true:false" 
                            size="small">{{courseInfo.teachingCase}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学资源"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad(3)" 
                            :disabled="courseInfo.teachingResources=='无'?true:false" 
                            type="text" 
                            size="small">{{courseInfo.teachingResources}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad(4)" 
                                :disabled="courseInfo.otherTeachingResources=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.otherTeachingResources}}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                <el-table-column
                    label=""
                    width="2px"
                ></el-table-column>
                <el-table-column 
                    label="学期作业文档"
                    align="center">
                    <el-table-column 
                        label="实验报告模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad(5)" 
                                :disabled="courseInfo.experimentalReportTemplate=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.experimentalReportTemplate}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="平时作业模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad(6)" 
                                :disabled="courseInfo.dailyWorkTemplate=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.dailyWorkTemplate}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad(7)" 
                                :disabled="courseInfo.otherTermAssignments=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.otherTermAssignments}}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                </el-table-column>
            </el-table>
        </div>
        <FileDownload v-if="searchDetail" @back="searchDetail=false" :type="type"></FileDownload>
    </div>
</template>

<script>
import FileDownload from './fileDownload.vue'
import {mapGetters,mapActions} from 'vuex'
export default {
    data(){
        return{
            searchDetail:false,
            type:0
        }
    },
    props:["chooseContent"],
    components:{
        FileDownload
    },
    computed:{
        ...mapGetters("stu",{
            courseInfo:"getCourseInfo"
        })
    },
    mounted(){
        this.requestInfo({content:this.chooseContent});
    },
    methods:{
        ...mapActions('stu',['requestInfo','requestDetail']),
        downLoad(type){
            this.requestDetail({id:this.chooseContent.courseInfoId,type});
            this.type=type;
            this.searchDetail=true;
        }
    }
}
</script>

<style lang="less" scoped>

</style>
