<template>
    <div class="updateDetail">
        <el-table
            :data="updateTable"
            stripe
            style="width: 100%">
            <el-table-column
                prop="courseFolderName"
                align="center"
                label="作业夹文件"
                width="400">
            </el-table-column>
            <el-table-column
                label="操作"
                align="center"
                width="400">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="text" size="small">上传</el-button>
                    <el-button @click="change(scope.row)" type="text" size="small">修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <FileUpload @setDrawer="setDrawer" :drawer="drawer" :chooseContent="chooseContent"></FileUpload>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
import FileUpload from './fileUpload.vue'
export default {
    data(){
        return{
            drawer:false,
            chooseContent:null
        }
    },
    computed:{
        ...mapGetters('stu',{
            updateTable:"getUpdateTable"
        })
    },
    components:{
        FileUpload
    },
    methods:{
        setDrawer(drawer){
            this.drawer=drawer
        },
        update(content){
            this.chooseContent=content;
            this.setDrawer(true);
        },
        change(){
            
        }
    }
}
</script>

