<template>
    <div class="myCourse">
        <el-table
             v-if="!showDetail"
            ref="multipleTable"
            :data="myCourse"
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column
                prop="serial"
                label="序号"
                width="80"
                align="center">
            </el-table-column>
            <el-table-column
                prop="courseName"
                label="课程名称"
                width="200"
                align="center">
            </el-table-column>
            <el-table-column
                label="课程操作"
                align="center">
                <template slot-scope="scope">
                    <el-button @click="changeCourse(scope.row)" type="text" size="small">修改课程</el-button>
                    <el-button @click="deleteCourse(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
            <el-table-column
                label="作业夹操作"
                align="center">
                <template slot-scope="scope">
                    <el-button @click="createClamp(scope.row)" type="text" size="small">创建</el-button>
                </template>
            </el-table-column>
            <el-table-column
                label="文档操作"
                align="center">
                <template slot-scope="scope">
                    <el-button @click="enterDetail(scope.row,0)" type="text" size="small">上传</el-button>
                    <el-button @click="enterDetail(scope.row,1)" type="text" size="small">下载</el-button>
                    <el-button @click="enterDetail(scope.row,2)" type="text" size="small">修改</el-button>
                    <el-button @click="enterDetail(scope.row,3)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <CourseDetail v-if="showDetail" @back="showDetail=false" :chooseContent="chooseContent" :handleType="handleType"></CourseDetail>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import CourseDetail from './courseDetail.vue'
export default {
    data(){
        return{
            showDetail:false,
            chooseContent:'',
            handleType:0
        }
    },
    components:{
        CourseDetail
    },
    computed:{
        ...mapGetters('teach',{
            myCourse:"getMyCourse",
            id:"getId",
        })
    },
    mounted(){
        this.requestMyCourse(this.id)
    },
    methods:{
        ...mapActions('teach',["requestMyCourse","requestCourseDetail","submitChangeCourse","submitDeleteCourse","submitCreateClamp"]),
        // 修改课程
        changeCourse(content){
            this.$prompt(`修改${content.courseName}的名字为`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({ value }) => {
                    this.submitChangeCourse({content,that:this})
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'+err
                    });       
                });
        },
        // 删除课程
        deleteCourse(content){
            this.$confirm(`确定删除${content.name}课程吗?\n删除包括您的作业内容和文档内容`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    this.submitDeleteCourse({content,that:this})
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
        },
        // 创建作业夹
        createClamp(content){
            this.$prompt(`请输入要创建的作业夹名称`, '创建作业夹', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                }).then(({ value }) => {
                    let name=value;
                    let that=this;
                    this.$prompt(`请输入相应的班级代号，多个班级用,（英文的逗号）隔开`,'选择班级',{
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                    }).then(({value})=>{
                        this.submitCreateClamp({name,classNo:value,id:content.courseInfoId,that});
                    }).catch(err=>{

                    })
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'+err
                    });       
                });
        },
        // 进入详细的目录
        enterDetail(content,type){
            this.handleType=type;
            this.showDetail=true;
            this.chooseContent=content;
            this.requestCourseDetail({content,type})
        }
    }
}
</script>

