<template>
    <div class="Header">
        <div class="headerContainer">
            <div>
                <img src="../../assets/img/logo.png">
                <span class="identify">{{identity}}</span>
            </div>
            <el-button @click="logOff" round icon="el-icon-warning">退出登录</el-button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    props:["identity"],
    methods:{
        logOff(){
            this.$confirm('是否确定退出登录?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                let that=this;
                axios.get('/user/exit')
                    .then(res=>{
                        if(res.data.judge){
                            that.$message({
                                type: 'success',
                                message:res.data.msg
                            });
                            that.$router.push('/')
                        }else{
                            that.$message({
                                type: 'error',
                                message:res.data.msg
                            });
                        }
                    })
                
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消退出登录'
                });          
            });
        }
    }
}
</script>

<style lang="less" scoped>
@import url("../../assets/less/define.less");
.Header{
    width: 100%;
    box-shadow: 0px 0px 10px @topicDeepBColor;
    .headerContainer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 1100px;
        margin: 0 auto;
        padding-bottom: 20px;
        &>div{
            display: flex;
            align-items: center;
        }
        .identify{
            margin-left: 20px;
            font-size: 36px;
            font-family: "楷体";
        }
    }
}
</style>
